import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {
  A2aClientHttpService as A2aClientService,
  DetailedExternalAccount,
  ExternalAccountGetPostPutResponse,
  ExternalAccountPostRequest,
  ExternalAccountVerificationPutRequest,
} from '@backbase/data-ang/payment-order-a2a';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

// temporary workaround: please check https://backbase.atlassian.net/browse/PAYM-1109
interface ExternalAccountVerificationPutRequestExtend {
  value1: string | number;
  value2: string | number;
}

@Injectable()
export class LinkAccountService {
  private isFormModalOpen = new BehaviorSubject<boolean>(false);

  private readonly defaultAccount: Partial<DetailedExternalAccount | ExternalAccountGetPostPutResponse> = {
    routingNumber: '',
    accountNickName: '',
    accountNumber: '',
  };

  readonly responseError$ = new BehaviorSubject<HttpErrorResponse | undefined>(undefined);
  readonly refreshAccounts$ = new BehaviorSubject(this.defaultAccount);
  readonly accounts$: Observable<DetailedExternalAccount[] | undefined> = this.refreshAccounts$.pipe(
    switchMap(() =>
      this.paymentOrdersA2ADataHttpService.getAll().pipe(
        catchError(err => {
          this.responseError$.next(err);
          return throwError(err);
        }),
        map(res => (res && res.accounts ? res.accounts : [])),
      ),
    ),
  );

  constructor(private readonly paymentOrdersA2ADataHttpService: A2aClientService) {}

  postNewAccount(contactItem: ExternalAccountPostRequest) {
    return this.paymentOrdersA2ADataHttpService.create({ externalAccountPostRequest: contactItem });
  }

  putNewAccountVerification(externalId: string, amounts: ExternalAccountVerificationPutRequestExtend) {
    return this.paymentOrdersA2ADataHttpService.verify({
      externalAccountId: externalId,
      externalAccountVerificationPutRequest: amounts as ExternalAccountVerificationPutRequest,
    });
  }

  removeAccount(externalAccountId: string) {
    return this.paymentOrdersA2ADataHttpService._delete({ externalAccountId });
  }

  get isModalOpen(): Observable<boolean> {
    return this.isFormModalOpen;
  }

  openModal(value = true) {
    this.isFormModalOpen.next(value);
  }
}
