import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import { EmptyStateModule } from '@backbase/ui-ang/empty-state';
import { HeaderModule } from '@backbase/ui-ang/header';
import { ConnectExternalAccountsJourneyUiModule } from './components/connect-external-accounts-journey-ui.module';
import { ConnectExternalAccountsJourneyComponent } from './connect-external-accounts-journey.component';
import { ConnectExternalAccountsJourneyConfigService } from './services/config.service';
import { ConnectExternalAccountsViewComponent } from './views/connect-external-accounts-view.component';

const defaultRoute: Route = {
  path: '',
  component: ConnectExternalAccountsJourneyComponent,
  children: [{ path: '', component: ConnectExternalAccountsViewComponent }],
};

const components = [ConnectExternalAccountsJourneyComponent, ConnectExternalAccountsViewComponent];
const uiModules = [EmptyStateModule, HeaderModule];

@NgModule({
  declarations: [...components],
  imports: [
    CommonModule,
    RouterModule,
    BackbaseCoreModule.withConfig({
      classMap: {
        ConnectExternalAccountsJourneyComponent,
      },
    }),
    ConnectExternalAccountsJourneyUiModule,
    ...uiModules,
  ],
  exports: [...uiModules, ...components],
  providers: [ConnectExternalAccountsJourneyConfigService],
})
export class ConnectExternalAccountsJourneyModule {
  static forRoot(
    data: { route: Route; [key: string]: any } = { route: defaultRoute },
  ): ModuleWithProviders<ConnectExternalAccountsJourneyModule> {
    return {
      ngModule: ConnectExternalAccountsJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
