import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { validateNumber } from '../../helpers/helpers';
import { DecimalFieldTypes } from '../../models/types';

@Component({
  selector: 'bb-activate-a2a-account',
  templateUrl: 'activate-account-form.component.html',
})
export class ActivateExternalAccountComponent {
  numberValidation = validateNumber;

  /**
   * Submitted form flag
   * Default value is false
   */
  @Input() submitted = false;

  /**
   * Activate account form
   */
  @Input() activateAccountForm!: FormGroup;

  /**
   * Form fields disabled flag
   * Default value is false
   */
  @Input() isDisabled = false;

  /**
   * Currency list
   */
  @Input() currencies: Array<string> = [];

  /**
   * Set decimals fields values event emitter
   */
  @Output() setDecimalsValues = new EventEmitter<DecimalFieldTypes>();

  currencyState = { currency: true, integer: true, decimal: false };

  isFieldValid(fieldName: string): boolean | undefined {
    const control = this.getControl(fieldName);

    return control?.valid;
  }

  private getControl(fieldName: string): AbstractControl | undefined {
    return this.activateAccountForm?.controls[fieldName];
  }

  onSetDecimalsValues(name: string) {
    const value = this.activateAccountForm.controls[name].value;
    const field = { name, value };
    this.setDecimalsValues.emit(field);
  }
}
