import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { BehaviorSubject, Subject } from 'rxjs';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';
import { validateNumber } from '../../helpers/helpers';
import { ModalState } from '../../models/types';

@Component({
  selector: 'bb-a2a-link-account-form',
  templateUrl: './link-account-form.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LinkAccountModalComponent implements OnChanges {
  readonly modalOptions = {
    backdrop: 'static',
    keyboard: false,
  };

  /**
   * Modal open flag
   * Default value is false
   */
  @Input() modalOpen = false;

  /**
   * Account form
   */
  @Input() accountForm!: FormGroup;

  /**
   * Submitted form flag
   * Default value is false
   */
  @Input() submitted = false;

  /**
   * Loading data flag
   * Default value is false
   */
  @Input() isLoading = false;
  /**
   * Form options input to pass options from journey
   */
  @Input() formInputLengths?: {
    name: number;
    number: number;
    routingNumber: number;
  };

  /**
   * Modal state flag
   */
  @Input() modalState!: BehaviorSubject<ModalState>;

  /**
   * Submit form event emitter
   */
  @Output() submitForm = new EventEmitter<void>();

  /**
   * Discard account form data event emitter
   */
  @Output() discardData = new EventEmitter<void>();

  /**
   * Build account form event emitter
   */
  @Output() buildForm = new EventEmitter<void>();

  confirmationModalOpen = false;
  numberValidator = validateNumber;
  gc$ = new Subject<void>();
  preventFormDiscard = false;
  readonly states = ModalState;
  state$: BehaviorSubject<ModalState> = new BehaviorSubject<ModalState>(ModalState.Hidden);

  isFieldValid(fieldName: string): boolean | undefined {
    const control = this.getControl(fieldName);

    return control?.valid;
  }

  private getControl(fieldName: string): AbstractControl | undefined {
    return this.accountForm?.controls[fieldName];
  }

  hasRequiredError(field: string): boolean | undefined {
    const fieldControl = this.getControl(field);
    if (!fieldControl || !fieldControl.errors) {
      return;
    }
    return fieldControl.errors.required;
  }

  onSubmit() {
    this.submitForm.emit();
    this.modalOpen = false;
    this.state$ = this.modalState;
  }

  onDiscard() {
    this.discardData.emit();
  }

  manageConfirmationModal() {
    if (this.preventFormDiscard) {
      this.confirmationModalOpen = true;
    } else {
      this.onDiscard();
      this.closeModal();
    }
  }

  openModal() {
    this.buildForm.emit();
    this.state$.next(ModalState.Preview);
  }

  closeModal() {
    this.state$.next(ModalState.Hidden);
  }

  ngOnChanges() {
    if (this.accountForm) {
      this.accountForm.valueChanges.pipe(takeUntil(this.gc$), distinctUntilChanged()).subscribe(formValues => {
        this.preventFormDiscard = Object.values(formValues).some(value => !!value);
      });
    }
  }
}
