import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DetailedExternalAccount } from '@backbase/data-ang/payment-order-a2a';
import { payverisStatuses } from '../../helpers/constants';
import { EventTypes } from '../../models/types';

@Component({
  selector: 'bb-a2a-accounts-list',
  templateUrl: './accounts-list.component.html',
})
export class AccountsListComponent {
  isOpen = false;
  isPendigAccountOpen = false;
  statuses = payverisStatuses;
  /**
   * Open link account modal event emitter
   */
  @Output() openModal = new EventEmitter();
  /**
   * Accounts list array
   */
  @Input() accounts?: DetailedExternalAccount[];
  /**
   * Activate account form
   */
  @Input() activateForm?: any;
  /**
   * Loading activate account flag
   * Default value is false
   */
  @Input() isLoading = false;
  /**
   * Open account details modal event emitter
   */
  @Output() selectExternalAccount = new EventEmitter();
  /**
   * Delete account event emitter
   */
  @Output() deleteAccount = new EventEmitter<string>();

  /**
   * Make transfer event emitter
   */
  @Output() makeTransfer = new EventEmitter<string>();

  confirmationModalOpen = false;
  accountInfo?: DetailedExternalAccount;

  onClickNewAccount() {
    this.openModal.emit();
  }

  populateAccount(account: DetailedExternalAccount) {
    this.selectExternalAccount.emit(account);
  }

  onKeydown(event: Partial<EventTypes>, account?: DetailedExternalAccount) {
    if (event.code === 'Space' || event.key === 'Enter') {
      if (account) {
        this.populateAccount(account);
      } else {
        this.onClickNewAccount();
      }
    }
  }

  stopPropagation(event: Event) {
    event.stopPropagation();
  }

  manageConfirmationModal(account: DetailedExternalAccount) {
    this.accountInfo = account;
    this.confirmationModalOpen = true;
  }

  onDeleteAccount(id: string) {
    this.deleteAccount.emit(id);
  }

  onMakeTransfer(account: DetailedExternalAccount) {
    this.makeTransfer.emit(account.externalAccountId);
  }
}
